body {
    margin: 0;
    background-color: #ccc;
}

* {
    box-sizing: border-box;
}

.App {
    background-color: #0B2434;
    height: 80vh;
    width: 600px;
    margin: auto;
    padding: 20px;
    margin-top: 30px;
    font-family: 'Karla', sans-serif;
}

main {
    background-color: #F5F5F5;
    border-radius: 5px;
    width: 90%;
    height: 90%;
    margin: auto;
    margin-top: 3%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.die {
    width: 55px;
    height: 55px;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.die-num {
    font-size: 30px;
}

.title {
    font-size: 42px;
    margin: 0;
}

.instructions {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    margin-top: 0;
    text-align: center;
    font-size: 22px;
    padding: 10px;
}

.container {
    display: grid;
    grid-template-columns: auto auto auto auto auto;
    gap: 25px;
}

.info {
    text-align: center;
}

.roll-btn,
.new-game-btn {
    width: 150px;
    height: 60px;
    border-radius: 6px;
    background-color: #5035FF;
    border: none;
    color: white;
    font-size: 1.2rem;
    font-family: 'Karla', sans-serif;
    cursor: pointer;
}

.roll-btn:focus,
.new-game-btn:focus {
    outline: none;
}

.roll-btn:active,
.new-game-btn:active {
    box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}

.winning-text {
    text-transform: uppercase;
    letter-spacing: 6px;
    transform: translateY(0);
    animation: fl 3s infinite linear;
}

@keyframes fl {
    30% {
        transform: translateY(10%) rotate(4deg);
    }
    70% {
        transform: translateY(10%) rotate(-4deg);
    }
}